import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../pages/HomeView.vue'
import PortfolioView from '../pages/PortfolioView.vue'
import ServicesView from '../pages/ServicesView.vue'
import Service from '../components/services/Service.vue'
import Blog from '../pages/Blog.vue'
import HireMeView from '../pages/HireMeView.vue'

import MainBlogPage from '../components/blog/MainBlogPage.vue'
import HtmlBlogPage from '../components/blog/HtmlBlogPage.vue'

const routes = [
  {
    path: '/',
    redirect: '/home',
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView,
  },
  {
    path: '/portfolio',
    name: 'portfolio',
    component: PortfolioView,
  },
  {
    path: '/services',
    name: 'services',
    component: ServicesView,
  },
  {
    path: '/blog',
    name: 'blog',
    component: Blog,
    // redirect: '/blog/main',
    children: [
      { path: '', redirect: '/blog/main' },
      { path: 'main', component: MainBlogPage },
      { path: 'html', component: HtmlBlogPage },
    ],
  },
  {
    path: '/services/:serviceName',
    component: Service,
    props: true,
  },
  {
    path: '/hireme',
    name: 'hireme',
    component: HireMeView,
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
})

export default router
